.rubric-item {
  overflow: hidden;
  background-size: auto, 1250px;
  background-position: center;
  background-repeat: no-repeat;
}
.rubric-item .container {
  position: relative;
  height: 537px;
}
.rubric-item__toggle {
  display: none;
}
.rubric-item__wrapper {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 75px 46px 60px 46px;
}
@media (max-width: 768px) {
  .rubric-item__wrapper {
    padding: 105px 60px 85px;
  }
}
.rubric-item__left {
  width: 50%;
}
.rubric-item__right {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.rubric-item__main-title {
  margin: 0 0 24px;
  font-size: 38px;
  line-height: 49px;
}
@media (max-width: 768px) {
  .rubric-item__main-title {
    font-size: 40px;
    line-height: 51px;
    margin-bottom: 28px;
    margin-top: 10px;
  }
}
.rubric-item__sub-title {
  margin: 0;
  line-height: 1.2;
  font-size: 30px;
  margin-bottom: 25px;
}
.rubric-item__text p {
  margin: 0;
  margin-bottom: 20px;
  font-size: 22px;
  line-height: 1.3;
}
.rubric-item--inner .container {
  width: auto;
}

@media (max-width: 1024px) {
  .rubric-item .container {
    height: auto;
  }
}
@media (max-width: 768px) {
  .rubric-item .container {
    height: auto;
  }
  .rubric-item__toggle {
    display: inline-block;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    cursor: pointer;
    padding: 0;
    padding-right: 30px;
    margin: 20px 0;
    text-align: left;
    color: #fff;
    background-color: transparent;
    outline: none;
    border: none;
    padding-left: 40px;
  }
  .rubric-item__toggle--v1 {
    position: relative;
  }
  .rubric-item__toggle--v1::before {
    content: "";
    position: absolute;
    width: 43px;
    height: 1px;
    background-color: #fff;
    top: 50%;
    left: 0;
  }
  .rubric-item__toggle--v1::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 75px;
    background-color: #fff;
    top: 50%;
    left: 0;
  }
  .rubric-item__wrapper {
    flex-wrap: wrap;
  }
  .rubric-item__wrapper--chronicle {
    padding: 60px 40px 0;
  }
  .rubric-item__left {
    width: 100%;
  }
  .rubric-item__right {
    display: block;
    width: 100%;
  }
  .rubric-item .button--arrow svg {
    margin-left: 5px;
    vertical-align: middle;
    fill: #fff;
    transition: transform 0.5s;
  }
  .rubric-item__toggle {
    padding: 0 30px 0 57px;
  }
}
@media (max-width: 768px) {
  .rubric-item__toggle--v1 {
    margin: 40px 0 60px;
  }
}
@media (max-width: 767px) {
  .rubric-item__wrapper {
    padding: 35px 15px;
  }
  .rubric-item__wrapper--chronicle {
    padding: 46px 15px 30px 10px;
  }
  .rubric-item__main-title {
    font-size: 21px;
    line-height: 1.2;
    margin-bottom: 20px;
    margin-top: 5px;
  }
  .rubric-item__sub-title {
    font-size: 19px;
    line-height: 1.2;
    margin-bottom: 18px;
  }
  .rubric-item__text p {
    font-size: 15px;
    line-height: 1.2;
  }
  .rubric-item__toggle {
    font-weight: 500;
    font-size: 13px;
    line-height: 17px;
    text-transform: uppercase;
    padding-left: 0;
  }
  .rubric-item__toggle--v1 {
    padding-left: 0;
    margin-top: 14px;
    margin-bottom: 30px;
  }
  .rubric-item__toggle--v1::after, .rubric-item__toggle--v1::before {
    display: none;
  }
}/*# sourceMappingURL=rubric-item.css.map */
